import React, { useState, useEffect, useCallback } from "react";
import {
  Card,
  CardDescription,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import type {
  StockData,
  StockHistory,
  TableType,
} from "../app/types/StockData";
import Loading from "@/components/fancy-dark-loading";
import {
  Banknote,
  BarChart2,
  Coins,
  Divide,
  Landmark,
  Percent,
  Scale,
  Share,
  Star,
  TrendingUp,
  User,
  Users,
} from "lucide-react";
import { SupabaseClient } from "@supabase/supabase-js";
import { createSupabaseClient } from "@/lib/supaBaseClient";

interface StockDataDisplayProps {
  data: TableType;
}

interface Strength {
  title: string;
  description: string;
}

export function StockDataDisplay({ data }: StockDataDisplayProps) {
  const [cachedData, setCachedData] = useState<TableType | null>(null);
  const [companyDescription, setCompanyDescription] = useState<string>("");
  const [imageSrc, setImageSrc] = useState<string>(
    "https://images.unsplash.com/photo-1456930266018-fda42f7404a7?q=80&w=1595&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D"
  );
  const [imageSrc2, setImageSrc2] = useState<string>(
    "https://images.unsplash.com/photo-1456930266018-fda42f7404a7?q=80&w=1595&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D"
  );
  const [keyMetrics, setKeyMetrics] = useState<
    Array<{ label: string; value: string | number; description?: string }>
  >([]);
  const [financialHealth, setFinancialHealth] = useState<
    Array<{ label: string; value: string | number; description?: string }>
  >([]);
  const [strengthsAndCatalysts, setStrengthsAndCatalysts] = useState<
    Strength[]
  >([]);
  const [analystHealth, setAnalystHealth] = useState<
    Array<{ label: string; value: string | number; description?: string }>
  >([]);
  const [risksAndMitigations, setRisksAndMitigations] = useState<Strength[]>(
    []
  );
  const [conclusion, setConclusion] = useState<string>("");
  const [hasImage, setHasImage] = useState<boolean>(false);
  const [loadingStates, setLoadingStates] = useState({
    companyOverview: true,
    keyMetrics: true,
    financialHealth: true,
    strengthsAndCatalysts: true,
    analystHealth: true,
    risksAndMitigations: true,
    conclusion: true,
    history: true,
  });
  const [client, setClient] = useState<SupabaseClient>();
  const [history, setHistory] = useState<StockHistory>();
  const [finImage, setFinImage] = useState<string>();

  useEffect(() => {
    setClient(createSupabaseClient());
    setLoadingStates((prev) => ({ ...prev, history: true }));
    const func = async () => {
      if (client) {
        const { data: imageData, error } = await client
          ?.from("images")
          .select("img")
          .eq("ticker", data.ticker)
          .single();
        if (imageData?.img) {
          setHasImage(true);
          setFinImage(imageData.img);
        }
      }
    };

    func();
    setLoadingStates((prev) => ({ ...prev, history: false }));
  }, [cachedData]);

  useEffect(() => {
    if (!cachedData) {
      setCachedData(data);
    }
  }, [data, cachedData]);

  const fetchCompanyOverview = useCallback(async () => {
    if (data.url1) setImageSrc(data.url1);
    setCompanyDescription(data.description);
    setLoadingStates((prev) => ({ ...prev, companyOverview: false }));
  }, [cachedData]);

  const fetchKeyMetrics = useCallback(async () => {
    if (cachedData) {
      try {
        const metricsData = [
          {
            label: "Market Cap",
            value: "$" + cachedData.marketCap,
            description: data.marketCapDsc,
          },
          {
            label: "Shares Outstanding",
            value: cachedData.sharesOutstanding,
            description: data.marketCapDsc,
          },
          {
            label: "Shares Float",
            value: cachedData.float,
            description: data.floatDsc,
          },
          {
            label: "EV/EBITDA",
            value: cachedData.evEbitda + "x",
            description: data.evEbitdaDsc,
          },
          {
            label: "P/E",
            value: cachedData.peTtm + "x",
            description: data.peTtmDsc,
          },
          {
            label: "Dividend Rate",
            value: "$" + cachedData.dividendRate,
            description: data.dividendRateDsc,
          },
        ];

        setKeyMetrics(metricsData);
      } catch (error) {
        console.error("Error fetching key metrics:", error);
        setKeyMetrics([]);
      } finally {
        setLoadingStates((prev) => ({ ...prev, keyMetrics: false }));
      }
    }
  }, [cachedData]);

  const fetchFinancialHealth = useCallback(async () => {
    if (cachedData) {
      const financialsData = [
        {
          label: "Cash Position",
          value: "$" + cachedData.cashPosition,
          description: data.cashPositionDsc,
        },
        {
          label: "Total Debt",
          value: "$" + cachedData.totalDebt,
          description: data.totalDebtDsc,
        },
        {
          label: "Debt to Equity",
          value: cachedData.debtToEquity + "x",
          description: data.debtToEquityDsc,
        },
        {
          label: "Current Ratio",
          value: cachedData.currentRatio + "x",
          description: data.currentRatioDsc,
        },
      ];

      setFinancialHealth(financialsData);
      setLoadingStates((prev) => ({ ...prev, financialHealth: false }));
    }
  }, [cachedData]);

  const fetchStrengthsAndCatalysts = useCallback(async () => {
    if (cachedData) {
      setStrengthsAndCatalysts(parsePoints(data.strengthsAndCatalysts));
      setLoadingStates((prev) => ({ ...prev, strengthsAndCatalysts: false }));
    }
  }, [cachedData]);

  const fetchAnalystHealth = useCallback(async () => {
    if (cachedData) {
      const analystInfo = [
        {
          label: "Analyst Rating (1-5)",
          value: cachedData.analystRating,
          description: cachedData.analystRatingDsc,
        },
        {
          label: "Number of Analysts",
          value: cachedData.numberOfAnalysts,
          description: cachedData.numberOfAnalystsDsc,
        },
        {
          label: "Mean Target Price",
          value: "$" + cachedData.meanTargetPrice,
          description: cachedData.meanTargetPriceDsc,
        },
        {
          label: "Implied +/-",
          value: cachedData.impliedChange,
          description: cachedData.impliedChangeDsc,
        },
      ];

      setAnalystHealth(analystInfo);
      setLoadingStates((prev) => ({ ...prev, analystHealth: false }));
    }
  }, [cachedData]);

  const fetchFinancialHistory = useCallback(async () => {
    if (client && cachedData && !history) {
      try {
        setLoadingStates((prev) => ({ ...prev, history: true }));

        const { data: row, error } = await client
          .from("company")
          .select(
            `
              revenue24,
              ebit24,
              netProfit24,
              ebitda24,
              roi24,
              revenue22,
              ebit22,
              netProfit22,
              ebitda22,
              roi22,
              revenue23,
              ebit23,
              netProfit23,
              ebitda23,
              roi23,
              revenue21,
              ebit21,
              netProfit21,
              ebitda21,
              roi21,
              oneYrDsc`
          )
          .eq("name", cachedData.name)
          .single();

        if (error) throw error;

        const req = { symbol: cachedData.ticker };
        const response = await fetch("/api/history", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(req),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const hD: StockHistory = await response.json();

        const obj: StockHistory = {
          name: cachedData.name,
          ticker: cachedData.ticker,
          revenue24: row?.revenue24 || hD.revenue24,
          ebit24: row?.ebit24 || hD.ebit24,
          netProfit24: row?.netProfit24 || hD.netProfit24,
          ebitda24: row?.ebitda24 || hD.ebitda24,
          roi24: row?.roi24 || hD.roi24,
          revenue22: row?.revenue22 || hD.revenue22,
          ebit22: row?.ebit22 || hD.ebit22,
          netProfit22: row?.netProfit22 || hD.netProfit22,
          ebitda22: row?.ebitda22 || hD.ebitda22,
          roi22: row?.roi22 || hD.roi22,
          revenue23: row?.revenue23 || hD.revenue23,
          ebit23: row?.ebit23 || hD.ebit23,
          netProfit23: row?.netProfit23 || hD.netProfit23,
          ebitda23: row?.ebitda23 || hD.ebitda23,
          roi23: row?.roi23 || hD.roi23,
          revenue21: row?.revenue21 || hD.revenue21,
          ebit21: row?.ebit21 || hD.ebit21,
          netProfit21: row?.netProfit21 || hD.netProfit21,
          ebitda21: row?.ebitda21 || hD.ebitda21,
          roi21: row?.roi21 || hD.roi21,
          dsc: row?.oneYrDsc || "",
        };

        setHistory(obj);
        console.log(obj);
      } catch (error) {
        console.error("Error fetching history:", error);
      } finally {
        setLoadingStates((prev) => ({ ...prev, history: false }));
      }
    }
  }, [cachedData]);

  const fetchRisksAndMitigations = useCallback(async () => {
    setLoadingStates((prev) => ({ ...prev, risksAndMitigations: false }));
    setRisksAndMitigations(parseRisksAndMitigations(data.risksAndMitigation));
  }, [cachedData]);

  const fetchConclusion = useCallback(async () => {
    if (data.url2) setImageSrc2(data.url2);
    setLoadingStates((prev) => ({ ...prev, conclusion: false }));
    setConclusion(data.conclusion);
  }, [cachedData]);

  useEffect(() => {
    if (cachedData) {
      fetchFinancialHistory();
      fetchCompanyOverview();
      fetchKeyMetrics();
      fetchFinancialHealth();
      fetchStrengthsAndCatalysts();
      fetchAnalystHealth();
      fetchRisksAndMitigations();
      fetchConclusion();
    }
  }, [
    cachedData,
    fetchFinancialHistory,
    fetchCompanyOverview,
    fetchKeyMetrics,
    fetchFinancialHealth,
    fetchStrengthsAndCatalysts,
    fetchAnalystHealth,
    fetchRisksAndMitigations,
    fetchConclusion,
  ]);

  if (!cachedData) {
    return <Loading />;
  }

  return (
    <div className="m-0 p-0">
      <div className="h-full flex flex-col gap-10 p-10 items-center justify-center bg-slate-900">
        {loadingStates.companyOverview ? (
          <LoadingCard />
        ) : (
          <CompanyOverview
            name={cachedData.name}
            description={companyDescription}
            imageSrc={imageSrc}
          />
        )}

        {loadingStates.companyOverview ? (
          <LoadingCard />
        ) : (
          <FinancialSnapshot
            image={finImage || ""}
            hasImage={hasImage}
            cacheData={history}
          />
        )}
        {loadingStates.keyMetrics ? (
          <LoadingCard />
        ) : (
          <KeyMetrics metrics={keyMetrics} />
        )}
        {loadingStates.financialHealth ? (
          <LoadingCard />
        ) : (
          <FinancialHealth financials={financialHealth} />
        )}
        {loadingStates.strengthsAndCatalysts ? (
          <LoadingCard />
        ) : (
          <StrengthsAndCatalysts strengths={strengthsAndCatalysts} />
        )}
        {loadingStates.analystHealth ? (
          <LoadingCard />
        ) : (
          <AnalystHealth analystData={analystHealth} />
        )}
        {loadingStates.risksAndMitigations ? (
          <LoadingCard />
        ) : (
          <RisksAnalysis points={risksAndMitigations} />
        )}
        {loadingStates.conclusion ? (
          <LoadingCard />
        ) : (
          <Conclusion
            rec={data.recommendation}
            description={conclusion}
            imageSrc={imageSrc2}
          />
        )}
      </div>
    </div>
  );
}

function CompanyOverview({
  name,
  description,
  imageSrc,
}: {
  name: string;
  description: string;
  imageSrc: string;
}) {
  return (
    <Card className="w-full max-w-[80vw] mx-auto bg-zinc-900 shadow-2xl shadow-cyan-400 text-white border-0 overflow-hidden">
      <div className="flex flex-col lg:flex-row">
        <CardHeader className="flex-1 p-4 sm:p-6 md:p-8 lg:p-12 justify-center">
          <CardTitle className="barlow-bold text-2xl sm:text-3xl md:text-4xl lg:text-5xl pb-3 font-bold text-center lg:text-left">
            <span className="bg-gradient-to-r from-purple-400 via-blue-500 to-indigo-400 text-transparent bg-clip-text">
              {name}
            </span>
          </CardTitle>
          <CardDescription className="montserrat text-sm sm:text-base md:text-lg lg:text-xl text-white">
            {description}
          </CardDescription>
          <div className="mt-4 sm:mt-6 md:mt-8 flex items-center">
            <div className="h-10 w-10 md:h-10 md:w-10 bg-white rounded-full overflow-hidden">
              <img
                className="object-cover w-full h-full"
                src="data:image/jpeg;base64,"
                alt="Adrian Saville"
              />
            </div>
            <h1 className="montserrat text-lg md:text-xl ml-3">
              by Adrian Saville
            </h1>
          </div>
        </CardHeader>
        <div className="w-full lg:w-5/12 h-48 sm:h-56 md:h-64 lg:h-auto relative overflow-hidden">
          <img
            className="object-cover w-full h-full"
            src={imageSrc || "/placeholder.svg"}
            alt={`${name} visual representation`}
          />
        </div>
      </div>
    </Card>
  );
}

function FinancialSnapshot({
  cacheData,
  hasImage,
  image,
}: {
  hasImage: boolean;
  image: string;
  cacheData: StockHistory | undefined;
}) {
  const financialData = [
    {
      year: 2024,
      revenue: cacheData?.revenue24,
      ebit: cacheData?.ebit24,
      netProfit: cacheData?.netProfit24,
      ebiTda: cacheData?.ebitda24,
      roi: cacheData?.roi24,
    },
    {
      year: 2023,
      revenue: cacheData?.revenue23,
      ebit: cacheData?.ebit23,
      netProfit: cacheData?.netProfit23,
      ebiTda: cacheData?.ebitda23,
      roi: cacheData?.roi23,
    },
    {
      year: 2022,
      revenue: cacheData?.revenue22,
      ebit: cacheData?.ebit22,
      netProfit: cacheData?.netProfit22,
      ebiTda: cacheData?.ebitda22,
      roi: cacheData?.roi22,
    },
    {
      year: 2021,
      revenue: cacheData?.revenue21,
      ebit: cacheData?.ebit21,
      netProfit: cacheData?.netProfit21,
      ebiTda: cacheData?.ebitda21,
      roi: cacheData?.roi21,
    },
  ];

  let data = cacheData?.dsc;
  console.log(data);
  return (
    <Card className="w-full max-w-[80vw] mx-auto bg-zinc-900 shadow-2xl shadow-cyan-400 text-gray-100 border-0 overflow-hidden">
      <CardHeader className="p-4 sm:p-6 md:p-8">
        <CardTitle className="barlow-bold text-2xl sm:text-3xl font-bold text-center">
          <span className="bg-gradient-to-r from-purple-400 via-blue-500 to-indigo-400 text-transparent bg-clip-text">
            Financial Snapshot: Stock's Performance
          </span>
        </CardTitle>

        <div className="grid pt-5 lg:grid-cols-2 gap-8">
          <div className="space-y-4">
            <h2 className="barlow-bold text-xl sm:text-xl font-bold text-center">
              <span className="bg-gradient-to-r from-purple-400 via-blue-500 to-indigo-400 text-transparent bg-clip-text">
                1-Year Stock Price
              </span>
            </h2>
            <div className="p-1">{data}</div>
            {hasImage && (
              <img
                src={image || "/placeholder.svg"}
                alt="Pasted image"
                className="w-full max-w-[681px] h-auto aspect-[681/416] object-cover mx-auto rounded-lg shadow-lg"
              />
            )}
          </div>

          <div className="space-y-4">
            <h2 className="barlow-bold text-xl sm:text-xl font-bold text-center">
              <span className="bg-gradient-to-r from-purple-400 via-blue-500 to-indigo-400 text-transparent bg-clip-text">
                4-Year Financials
              </span>
            </h2>
            <div className="rounded-lg overflow-x-scroll text-center">
              <table className="w-full">
                <thead className="bg-zinc-800">
                  <tr>
                    <th className="text-gray-100 p-2">Year</th>
                    <th className="text-gray-100 p-2">Revenue ($B)</th>
                    <th className="text-gray-100 p-2">EBIT ($B)</th>
                    <th className="text-gray-100 p-2">Net Profit</th>
                    <th className="text-gray-100 p-2">EBITDA ($B)</th>
                    <th className="text-gray-100 p-2">ROI (%)</th>
                  </tr>
                </thead>
                <tbody>
                  {financialData.map((row) => (
                    <tr key={row.year} className="border-zinc-800">
                      <td className="text-gray-100 p-2">
                        {row.year.toString()}
                      </td>
                      <td className="text-gray-100 p-2">{row.revenue}</td>
                      <td className="text-gray-100 p-2">{row.ebit}</td>
                      <td className="text-gray-100 p-2">{row.netProfit}</td>
                      <td className="text-gray-100 p-2">{row.ebiTda}</td>
                      <td className="text-gray-100 p-2">{row.roi}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </CardHeader>
    </Card>
  );
}

function KeyMetrics({
  metrics,
}: {
  metrics: Array<{
    label: string;
    value: string | number;
    description?: string;
  }>;
}) {
  return (
    <Card className="w-full max-w-[80vw] mx-auto bg-zinc-900 shadow-2xl shadow-cyan-400 text-gray-100 border-0 overflow-hidden">
      <CardHeader className="p-4 sm:p-6 md:p-8 lg:p-12">
        <CardTitle className="barlow-bold text-2xl sm:text-3xl font-bold text-center pb-4 sm:pb-6">
          <span className="bg-gradient-to-r from-purple-400 via-blue-500 to-indigo-400 text-transparent bg-clip-text">
            Key Market Metrics: Reflecting Value and Potential
          </span>
        </CardTitle>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
          {metrics.map((metric, index) => (
            <div
              key={metric.label}
              className="montserrat p-4 sm:p-6 bg-gray-800 rounded-md text-center"
            >
              <p className="text-lg sm:text-xl font-bold">{metric.value}</p>
              <p className="text-sm sm:text-base font-semibold mt-2">
                {metric.label === "Market Cap" && (
                  <GradientIcon
                    icon={Coins}
                    gradientId={`gradient-km-${index}`}
                  />
                )}
                {metric.label === "Shares Outstanding" && (
                  <GradientIcon
                    icon={Users}
                    gradientId={`gradient-km-${index}`}
                  />
                )}
                {metric.label === "Shares Float" && (
                  <GradientIcon
                    icon={Users}
                    gradientId={`gradient-km-${index}`}
                  />
                )}
                {metric.label === "P/E" && (
                  <GradientIcon
                    icon={BarChart2}
                    gradientId={`gradient-km-${index}`}
                  />
                )}
                {metric.label === "EV/EBITDA" && (
                  <GradientIcon
                    icon={TrendingUp}
                    gradientId={`gradient-km-${index}`}
                  />
                )}
                {metric.label === "Dividend Rate" && (
                  <GradientIcon
                    icon={Percent}
                    gradientId={`gradient-km-${index}`}
                  />
                )}
                {metric.label}
              </p>
              <p className="text-sm sm:text-base mt-2 text-gray-400">
                {metric.description}
              </p>
            </div>
          ))}
        </div>
      </CardHeader>
    </Card>
  );
}

